
import $ from 'jquery'
import XImg from 'vux/src/components/x-img/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
components: {
XImg, Scroller
},
data () {
return {
url: 'https://detail.tmall.hk/hk/item.htm?spm=a1z10.10044-b-s.196.2.1e15154du716Y9&id=573893162110&scene=taobao_shop'
}
},
mounted () {
setTimeout(() => {
$('.btn-img').on('touchstart', function () {
$(this).css({transform: 'scale(0.9)'})
})
$('.btn-img').on('touchend', function () {
$(this).css({transform: 'scale(1)'})
})
}, 300)

setTimeout(() => {
this.$nextTick(() => {
this.$refs.scroller.reset()
})
}, 1000)
setTimeout(() => {
this.$nextTick(() => {
this.$refs.scroller.reset()
})
}, 2000)
},
methods: {
onBtnClick () {
if (window.is_wx()) {
let a = new window.ClipboardJS('.img-btn')
console.log(a)
this.$vux.toast.text(`<span style="font-size: 30px;"></span>`)
} else {
window.location.href = this.url
}
}
}
}



